<template>
  <div class="agreement">
    <navigation-bar  @onLeftClick="goBack" pageName="发布房源协议" :navBarStyle="navStyle" leftImgType="1"/>
    <div class="box" v-html="agreement"></div>
  </div>
</template>

<script>
import leftIcon from "@/assets/images/common/nav_return_white.png";
import { getHouseAgreement } from "../../../services/houseRentalService";
import NavigationBar from '@/common/currency/NavigationBar.vue';
export default {
  name: "agreement",
  data() {
    return {
      leftIcon: leftIcon,
      agreement: "",
      navStyle: {
        "background": "#00d2ca",
        "color": "#ffffff"
      }
    };
  },
  created() {
    this.getAgreement();
  },
  components: {
    NavigationBar
  },
  methods: {
    /**
     * 页面返回事件
     */
    goBack() {
      this.$router.go(-1);
    },

    /**
     * 获取房源协议
     */
    getAgreement() {
      getHouseAgreement({
        data: JSON.stringify({
          PROTOCOL_CODE: "2"
        })
      }).then(
        result => {
          this.agreement = result.data.CONTENT;
        },
        error => {
          this.$toast(error);
        }
      );
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.agreement {
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  .left {
    height: 46px;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      width: 26px;
      height: 26px;
    }
  }
  .box {
    padding: 12px;
    flex: 1;
    width: 100%;
    box-sizing: border-box;
    overflow: scroll;
  }
  /deep/.van-nav-bar {
    background: #00d2ca;
  }
  /deep/.van-nav-bar__title {
    color: #ffffff;
  }
}
</style>
